<template>
	<section class="hero is-fullheight is-danger is-bold">
		<div class="hero-body">
			<div class="container">
				<div class="betterUsers">
					<div class="columns">
						<div class="column is-4" v-for="(user,index) in users" :key="index">
							<div class="is-one-of-better-users has-background-white">
								<div class="image-container">
									<center>
										  <img class="is-rounded" src="https://bulma.io/images/placeholders/128x128.png">
									</center>
								</div>
								<div class="info-container has-text-dark has-text-centered">
									<p class="title is-4  has-text-dark has-text-weight-light">
										Lautaro Umpierrez
									</p>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</section>
</template>
<script>
	import { db } from '@/main';
	export default{
		props:['cantidad'],
		mounted(){
			this.getBetterUsers();
		},
		data:()=>({
			users:[]
		}),
		methods:{
			getBetterUsers(){
				db.collection('users').orderBy('victorias','desc').limit(this.cantidad).get().then((querySnapshot)=>{
					querySnapshot.forEach((user)=>{
						this.users.push(user.data());
					})
				});

			}
		},
		computed:{

		}
	}
</script>
<style>
	.betterUsers .columns .column{
		padding: 10px;
	}
	.betterUsers .columns .is-one-of-better-users .image-container img{
		width: 200px !important;
		border-radius: 50%;
		position: relative;
		margin-top: calc(22.5vh - 100px);
	}
	.betterUsers .columns .is-one-of-better-users .image-container{
		width: 100%;
		border-radius: 3px;
		height: calc(45vh + 5px) !important;
		background-color: rgb(245,245,245);
	}
	.betterUsers .columns .is-one-of-better-users
	{
		border-radius: 3px;
		height: calc(90vh - 10px);	

	}
	.info-container{
		padding: 10px !important;
		width: 100%;
		border-radius: 3px;
		height: calc(45vh - 5px) !important;
	}
</style>


