<template>
	<div>
		<nav class="navbar is-info">
			<div class="navbar-brand">
				<a class="navbar-item" @click="goBack()">
					<i class="fas fa-arrow-left"></i>&nbsp;
					GO BACK
				</a>
			</div>
		</nav>
		<br>
		<chart :data="[userState.victorias, userState.derrotas]" :options="{}"></chart>	
	</div>
</template>
<script>
	import chartComp from '@/components/member-side/chart';	
	export default{
		mounted(){
			
		},
		components:{'chart':chartComp},
		data:()=>({
			chartData:null,
			chartOptions:null,
		}),
		methods:{
			goBack(){
				this.$router.go(-1);
			},
			setDerrotasAndVictorias(){
				this.dataInfo = [this.userState.victorias, this.userState.derrotas];
			}
		},
		computed:{
			userState()
			{
				return this.$store.getters.getUserInfo;
			}
		}
	}
</script>


